






































.main {
  width: 100%;
  height: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  left: 50%;
  top: 30%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
}
.main p {
  font-size: 20px;
  text-align: center;
  margin-top: 20px;
}
#meet {
  color: #334455;
}
/* 心的位置 */
.heart {
  position: absolute;
  top: 0;
  right: 0;
}
.love {
  display: block;
  width: 100px;
  height: 100px;
  background: url(../../static/img/love.313656d.png) 0 0 no-repeat;
  background-size: 2900%;
  -webkit-animation: heart-burst steps(28) 0.8s infinite both;
          animation: heart-burst steps(28) 0.8s infinite both;
}
@-webkit-keyframes heart-burst {
0% {
    background-position: 0%;
}
100% {
    background-position: 100%;
}
}
@keyframes heart-burst {
0% {
    background-position: 0%;
}
100% {
    background-position: 100%;
}
}
